import React, { FC, useState } from 'react'
import clsx from 'clsx'
import { useEffectAfterMount } from '../../../hooks'
import { SwitcherProps } from './types'
import './Switcher.scss'

const Switcher: FC<SwitcherProps> = ({
  id,
  name,
  className,
  checked = false,
  disabled = false,
  fullChangeControl = false,
  onChange,
  isPlain = false,
  type = 'Secondary',
}) => {
  const [isChecked, setIsChecked] = useState(checked)

  useEffectAfterMount(() => {
    if (!fullChangeControl) {
      onChange(isChecked)
    }
  }, [isChecked])

  return (
    <label
      className={clsx(
        `Switcher ${disabled ? '--Disabled' : ''}`,
        className,
        `--${type}`,
      )}
      htmlFor={id}
    >
      <input
        checked={fullChangeControl ? checked : isChecked}
        className='Switcher__Input'
        disabled={disabled}
        id={id}
        name={name}
        onChange={
          fullChangeControl
            ? onChange
            : (): void => setIsChecked((value) => !value)
        }
        type='checkbox'
      />
      <span className={`Switcher__Slider ${isPlain && '--Plain'}`} />
    </label>
  )
}

export default Switcher
